
/*

	Header & Menu

*/

header{
	position: absolute;
	top: 0;
	width: 100%;
	padding-top: 2rem;
	z-index: 9;
	.container{
		max-width: none;
	}
	.menu__logo{
		margin-left: 3rem;
		float: left;
		width: 30%;
		img{
			width: 65px;
		}
	}
}

.menu-list {
	width: 100%;
	height: 0;
	overflow: hidden;
	visibility: hidden;
	position: absolute;
	transition: none;
	ul{
		float: none;
		padding: 0;
		margin-top: -0.5rem;
		margin-right: 5.1rem;
		li{
			float: none;
			list-style: none;
			margin-left: 1rem;
			text-align: right;
			a{
				font-size: 1rem;
				font-weight: 700;
				color: $color-blue;
					img{
						max-width: 25px;
					}
				&:hover{
					opacity: .5;
				}
				&.menu-list__here{
					cursor: default;
					opacity: .5;
				}
			}
			&.active {
				a {
					color: $color-white;
				}
			}
		}
	}
	@include media(tablet) {
		width: 100%;
		overflow: initial;
		height: auto;
		padding: 0;
		visibility: visible;
		position: relative;
		transition: none;
		ul{
			float: right;
			padding: 0;
			margin-top: -0.5rem;
			li{
				float: left;
				list-style: none;
				margin-left: 1rem;
			}
		}
	}
}
.menu{
	position: relative;
	.menu__button {
		display: inline-block;
		line-height: 4rem;
		position: absolute;
		top: 0;
		right: 5.1rem;
		@include media(mobile) {
			right: 3rem;
		}
		width: 1.5rem;
		img {
			position: relative;
			top: 1px;
		}
		@include media(tablet) {
			display: none;
		}
		svg{
			position: relative;
			top: 1px;
			fill: transparent;
			path{
				fill: $color-white;
			}
		}
	}
}


// Mobile menu

body.menu--on .menu .menu__nav {
	position: fixed;
	overflow: auto !important;
	height: auto;
	width: 100%;
	left: 0;
	top: 0;
	bottom: 0;
	background-color: $color-darkgray;
	padding: 2rem;
	box-sizing: border-box;
	transition: height 200ms $bezier;
	visibility: visible;
	z-index: 99999;
	ul {
		margin: 0;
		li {
			float: none !important;
			a {
				font-size: 1.25rem;
			}
			&:before {
				left: auto !important;
				right: 0.5rem;
				bottom: 2rem !important;
				width: 3rem;
			}
		}
	}
}