
/*

	Card

*/

.card {
	position: relative;
	strong {
		font-weight: 700;
	}
	ul {
		list-style: none;
		padding: 0;
	}
	.container {
		position: relative;
	}
	&.card--intro {
		.col-content {
			padding-top: 3rem;
			max-width: 14rem;
		}
		p{
			font-size: 0.7rem;
			line-height: 1rem;
		}
	}

	&.card--grid {
		.container {
			padding: 0;
		}
		p {
			font-size: .7rem;
			font-weight: 500;
			line-height: 1rem;
		}
		.col-content {
			padding: 1.5rem;
		}
		.card__col--light {
			background: $color-gray;
		}
		.card__col--bottom .col-content {
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			padding-bottom: 0.5rem;
		}
		.card__col-icon {
			position: absolute;
			margin: 0;
		}
		.card__col-icon--bottom {
			bottom: 1.5rem;
		}
		.card__col-icon--top {
			bottom: 1.5rem;
		}
	}
	&.card--hover {
		.card__col--cta {
			padding: 0 2rem 4rem 2rem;
		}
	}
	&.card--adaptive {
		height: auto;
		.col-content {
			padding-bottom: 3rem;
		}
	}

	.card__layer {
		position: absolute;
		z-index: 0;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.card__layer--obj {
			position: absolute;
		}
		// Pos
		.card__layer--x-0  { left: 0; }
		.card__layer--x-1  { left: 100% / 12; }
		.card__layer--x-2  { left: 100% / 12 * 2; }
		.card__layer--x-3  { left: 100% / 12 * 3; }
		.card__layer--x-4  { left: 100% / 12 * 4; }
		.card__layer--x-5  { left: 100% / 12 * 5; }
		.card__layer--x-6  { left: 100% / 12 * 6; }
		.card__layer--x-7  { left: 100% / 12 * 7; }
		.card__layer--x-8  { left: 100% / 12 * 8; }
		.card__layer--x-9  { left: 100% / 12 * 9; }
		.card__layer--x-10 { left: 100% / 12 * 10; }
		.card__layer--x-11 { left: 100% / 12 * 11; }
		.card__layer--y-0  { top: 0; }
		.card__layer--y-0-5  { top: 12.5%; }
		.card__layer--y-1  { top: 25%; }
		.card__layer--y-1-5  { top: 37.5%; }
		.card__layer--y-2  { top: 50%; }
		.card__layer--y-2-5  { top: 62.5%; }
		.card__layer--y-3  { top: 75%; }
		.card__layer--y-3-5  { top: 87.5%; }
		// Size
		.card__layer--h-0-5  { height: 12.5%; }
		.card__layer--h-1  { height: 25%; }
		.card__layer--h-2  { height: 50%; }
		.card__layer--h-3  { height: 75%; }
		.card__layer--h-4  { height: 100%; }
		.card__layer--w-r  { width: 1rem; }
		.card__layer--w-1  { width: 100% / 12; }
		.card__layer--w-2  { width: 100% / 12 * 2; }
		.card__layer--w-3  { width: 100% / 12 * 3; }
		.card__layer--w-4  { width: 100% / 12 * 4; }
		// Types
		.card__layer--line-h {
			border-left: 3px solid $color-darkgray;
			&.card__layer--white { border-color: $color-white; }
			&.card__layer--blue { border-color: $color-blue; }
			&.card__layer--dark { border-color: $color-darkgray; }
			&.card__layer--light { border-color: $color-gray; }
		}
		.card__layer--box {
			&.card__layer--white { background: $color-white; }
			&.card__layer--blue { background: $color-blue; }
			&.card__layer--dark { background: $color-darkgray; }
			&.card__layer--light { background: $color-gray; }
		}
	}
	.card-head{
		.col-content{
			padding: 0;
			margin-bottom: 1rem;
		}
	}
}