/*

	Config

*/

// Colors

$color-white: #FFFFFF;
$color-blue: #3A98CB;
$color-green: #239D87;
$color-purple: #A35ABF;
$color-gray: #C4DFE1;
$color-darkgray: #153648;

$color-text: $color-darkgray;
$color-textmeta: #C4DFE1;

$transitionBezier: all 250ms cubic-bezier(0.400, 0.150, 0.000, 0.975);

// Shadow

$shadow: 0 2px 8px 0 rgba(#000, 0.1);
$shadow-large: 0 2px 32px 0 rgba(#000, 0.1);
$shadow-over: 0 4px 32px 0 rgba(#000, 0.2);

// Transition

$bezier: cubic-bezier(0.400, 0.150, 0.000, 0.975);
$transition: all 250ms $bezier;

// Break-points

$gutterWidth: 0;
$screenMobile: 	768px;
$screenTablet: 	992px;
$screenDesktop: 1367px;
$screenLarge:   1601px;

@mixin media($point) {
    @if $point == mobile {
        @media (max-width: $screenTablet) { @content; }
    }
    @else if $point == tablet {
        @media (min-width: $screenTablet) { @content; }
    }
    @else if $point == desktop {
        @media (min-width: $screenDesktop) { @content; }
    }
    @else if $point == large {
        @media (min-width: $screenLarge)  { @content; }
    }
}
