
/*

	Form

*/
.section-form-fields{
	.section-form-field--title{
		h2{
			padding: 0 0 1rem 0;
			font-weight: 100;
			text-transform: none;
			float: left;
		}
		img{
			float: right;
			width: 1rem;
			&.disable{
				opacity: .5;
			}
		}
	}
	.section-form-field--guest{
		margin-bottom: 1rem;
		border-bottom: 1px solid black;
		&:last-child{
			border-bottom: none;
		}
		.section-form-field--title{
			h2{
				font-weight: 800;
			}
		}
	}
	.section-form-field--extra{
		&:last-child{
			border-bottom: none;
		}
		h2{
			text-transform: none;
			padding: .3rem 1rem;
		}
		p{
			text-transform: none;
			font-size: .8rem;
			line-height: 1rem;
		}
		border-bottom: 1px solid black;
	}
  .section-form-field_group{
    span{
      color: $color-white;
    }
    .error{
      padding: 1em;
      h3, span{
        color: $color-white;
        margin: 0;
      }
    }
		margin: 0 0 1em 0;
		padding: 0;
    fieldset{
      border: none;
      margin: 0;
      padding: 0;
      legend{
        text-transform: uppercase;
        color: $color-white;
        margin-bottom: 1em;
      }
    }
    .control {
    	position: relative;
    	cursor: pointer;
      padding: 0 0 0 2rem;
	  display: block;
	  span {
		font-size: 0.7rem;
		opacity: 0.65;
		display: block;
	}
      input{
        position: absolute;
      	z-index: -1;
      	opacity: 0;
        &:checked ~ .control__indicator {

          &:after {
            display: block;
          }
        }
        &:disabled ~ .control__indicator {
          pointer-events: none;
          opacity: .6;
          background: #fff;
        }
      }
      .control__indicator{
        position: absolute;
      	top: 4px;
      	left: 0;
      	width: 24px;
      	height: 24px;
      	background: $color-purple;
        border: none;
		border-radius: 4px;
        &:after {
          position: absolute;
          display: none;
          content: '';
        }
      }
      &.control--checkbox{
        .control__indicator{
          &:after {
           top: 6px;
           left: 5px;
           width: 16px;
           height: 12px;
			 content: "";
			 background-image: url("/img/tick.svg");
			 background-repeat: no-repeat;
           background-color: transparent;
           border-width: 0 2px 2px 0;
		   background-size: cover;
          }
        }
      }
      &.control--radio {
        .control__indicator{
          border-radius: 50%;
          &:after {
            top: 5px;
            left: 5px;
            width: 14px;
            height: 14px;
            border-radius: 50%;
            background: $color-white;
          }
        }
        input{
          &:disabled ~ .control__indicator:after {
            background: #7b7b7b;
          }
        }
      }
    }
    input,
    select,
    textarea{
      	background-color: $color-white;
      	height: 50px;
      	padding: 0 1em;
      	color: $color-text;
		box-sizing: border-box;
		width: 100%;
		border-radius: 4px;
		border-color: transparent;
    }
    textarea{
      height: auto;
      padding-top: .5em;
      resize: none;
    }
    input{
      &[type="checkbox"],[type="radio"] {
        width: 32px;
      }
    }
    label{
	  color: $color-white;
    }
    .submit{
      float: right;
      margin: 0;
      &.btn, &.btn:visited{
        color: $color-white;
        border-color: $color-white;
        &:hover{
          background-color: $color-white;
          color: $color-white;
        }
      }
    }
    .select {
    	position: relative;
    	width: 100%;
			background-color: $color-white;
			height: 50px;
			border-radius: 4px;
      select {
      	width: 100%;
				position: absolute;
				z-index: 1;
      	padding: 10px 1em;
      	cursor: pointer;
      	color: $color-text;
        height: 50px;
        line-height: 1em;
      	border-radius: 0;
      	outline: 0;
      	background: transparent;
      	appearance: none;
      	-webkit-appearance: none;
      	-moz-appearance: none;
				border: none;
        &:-ms-expand {
        	display: none;
        }
        &:hover, &:focus{
          color: $color-text;
        }
        &:hover ~ .select__arrow, &:focus ~ .select__arrow {
        	border-top-color: $color-text;
        }
				~ .select__arrow--btn{
        	background-color: $color-purple;
        }
				&:hover ~ .select__arrow--btn, &:focus ~ .select__arrow--btn {
        	background-color: $color-blue;
        }
        &:disabled{
          pointer-events: none;
        	opacity: .5;
        }
        &:disabled ~ .select__arrow {
        	border-top-color: #ccc;
        }
      }
    }
    .select__arrow--btn {
			top: 0px;
			right: 0px;
			position: absolute;
			width: 51px;
			height: 50px;
			background-color: $color-purple;
			border-radius: 0 4px 4px 0;
			.select__arrow {
		    	position: absolute;
		    	top: 0px;
		    	bottom: 50%;
		    	right: 14px;
				transform: translateY(50%);
		    	pointer-events: none;
	    	}
		}
		.select--datepicker{
			input{
				z-index: 1;
				background-color: transparent;
			    position: absolute;
			    border: none;
				cursor:pointer;
				&:hover ~ .select__arrow--btn, &:focus ~ .select__arrow--btn {
					background-color: $color-blue;
				}
			}
			.select__arrow--btn{
				.select__arrow {
					right: 4px;
					top: 0px;
					img{
						width: 75%;
					}
				}
			}
		}
  }
}