// Fonts

$serif: Georgia, Cambria, "Times New Roman", serif;
$sans: "proxima-nova", Helvetica, Arial, sans-serif;

h1, h2, h3 {
	font-family: $sans;
	font-weight: 800;
	letter-spacing: 0;
	line-height: 1;
	margin: 0;
}

h1 {
	font-size: 1.8rem; // 36px
	line-height: 1;
	span{
		font-weight: 100;
	}
}

h2 {
	font-size: 1rem; // 24px
	line-height: 1;
	font-weight: 800;
}

p {
	line-height: 1rem;
	font-weight: 400;
	line-height: 1.5rem;
}

h1, h2, h3, p {
	text-rendering: optimizeLegibility;
	-moz-font-feature-settings:"kern" 1;
	-moz-font-feature-settings:"kern=1";
	-ms-font-feature-settings:"kern" 1;
	-o-font-feature-settings:"kern" 1;
	-webkit-font-feature-settings:"kern" 1;
	font-feature-settings:"kern" 1;
}

.meta {
	color: $color-textmeta;
	font-family: $sans;
}

.title.meta {
	text-align: center;
	padding-bottom: 1em;
	text-transform: uppercase;
}

.align-center {
	text-align: center;
}

.align-right {
	text-align: right;
}