// footer
footer{
	margin-top: 3rem;
	.footer--col{
		background-color: $color-green;
		padding: 1rem 0rem;
		color: $color-white;
		.menu__logo{
			text-align: center;
			img{
				height: 66px;
				padding: 2rem 0 0;
			}
		}
		.menu-list--footer{
			padding: 1rem 0 0;
			.col-content{
				margin: 0 auto;
			}
			ul{
				margin: 0;
				padding: 0;
				li{
					margin-left: 0;
					margin-right: .5rem;
					float: left;
					list-style: none;
					margin-left: 1rem;
					text-align: right;
					a{
						color: $color-white;
						font-size: 0.7rem;
					}
					&.menu-list__here{
						a{
							color: $color-textmeta;
						}
					}
				}
				&.menu-list--social{
					margin: 0 auto;
					width: 186px;
					height: 30px;
					float: none;
					li{
						margin: 0;
						padding: 0 1px;
						&:last-child{
							margin-right: 0;
						}
						a{
							padding: 15px;
							display: inline-block;
							img{
								max-width: 30px;
							}
							&:hover{
								img{
									opacity: .5;
								}
							}
						}
					}
				}
				&.menu-list--menu{
					margin: 0 auto;
					display: none;
					padding: 1rem;
					li{
						&:last-child{
							margin-right: 0;
						}
						&.address{
							font-size: 0.7rem;
							padding: 7px 0;
							address{
								float: left;
								padding-right: 3px;
							}
						}
						a{
							&.menu-list__here{
								cursor: default;
								opacity: .5;
							}
							&:hover{
								opacity: .5;
							}
						}
					}
				}
			}
		}
	}
}