

// Booking form
.booking {
	position: fixed;
	right: 0;
	width: 100%;
	top: 0;
	height: 100%;
	z-index: -10;
	box-shadow: 0 6px 1rem rgba(0,0,0,0.15);
	background: $color-text;
	opacity: 0;
	transition: $transitionBezier;
	transform: translateX(2rem);
	color: $color-white;
	text-transform: none;
	h2{
		padding: 0;
	}
	p{
		font-size: .8rem;
		line-height: 1rem;
	}
	.booking__top{
		background-color: fade-out(black, .8);
		height: 3rem;
		position: relative;
		h2{
			padding: 1rem;
		}
		.booking__top--title{
			float: left;
			width: 15rem;
			color: $color-green;
			height: 3rem;
			padding: 0 1rem;
			box-sizing: border-box;
		}
		.booking__top--btn{
			width: 3rem;
			position: absolute;
			top: 0;
			right: 1rem;
			a{
				color: $color-white;
				display: block;
				height: 100%;
				width: 100%;
				box-sizing: border-box;
				padding: 1rem;
				background-color: transparent;
				text-align: center;
				color: $color-white;
				&:link, &:visited{
					color: $color-white;
				}
				&:hover {
					opacity: .65;
				}
			}
		}
	}
	.booking__form{
		padding: 2rem;
		height: calc(100vh - 7rem);
		overflow-y: auto;
		&.ps:hover>.ps__scrollbar-y-rail:hover{
			background-color: transparent;
		}
		&.ps .ps__scrollbar-y-rail{
			top: 3rem !important;
		}
		.form{
			height: 100%;
		}
		button {
			font-weight: 800;
		}
	}
	.diving__total{
		text-align: center;
		background-color: fade-out(black, .8);
		height: 3rem;
		position: absolute;
		bottom: 3.5rem;
		width: 100%;
		.diving__total--label{
			h2{
				font-weight: 100;
				text-transform: uppercase;
			}
		}
	}
	.diving__status{
		height: .5rem;
		background-color: black;
		position: absolute;
		bottom: 3rem;
		width: 100%;
		transition: $transitionBezier;
		background: rgb(163,90,191); /* Old browsers */
		background: -moz-linear-gradient(45deg, rgba(163,90,191,1) 0%, rgba(63,149,203,1) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(45deg, rgba(163,90,191,1) 0%,rgba(63,149,203,1) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(45deg, rgba(163,90,191,1) 0%,rgba(63,149,203,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a35abf', endColorstr='#3f95cb',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
	}
	.diving__btn{
		height: 3rem;
		text-align: center;
		width: 100%;
		margin-bottom: 1rem;
		margin-top: 1.75rem;
		button{
			background-color: $color-purple;
			text-align: center;
			border-color: transparent;
			border-radius: 42px;
		}
		span{
			//display: block;
			margin: 0 auto;
			font-weight: 500;
			opacity: 0.65;
			&.diving__btn--done{
				width: 96px;
			}
			&.diving__btn--checkout{
				width: 154px;
			}
		}
		h2{
			text-transform: uppercase;
		}
		img{
			float: left;
			padding: .8rem 0 0 1rem;
		}
		.btn--next{
			display: block;
			width: 100%;
			height: 100%;
			transition: $transitionBezier;
			&:link, &:visited{
				color: $color-white;
			}
			&:hover{
				background-color: $color-blue;
				color: $color-white;
			}
		}
	}
	.diving__paypal{
		position: absolute;
		bottom: 0;
		text-align: center;
		text-transform: none;
		color: $color-green;
		height: 2.5rem;
		width: 100%;
		a{
			width: 166px;
			display: block;
			margin: 5px auto;
			color: $color-green;
			&:hover{
				p, img{
					opacity: .5;
					color: $color-green;
				}
			}
			p{
				margin: .5rem 0 0 0;
				float: left;
			}
			img{
				width: 3rem;
				float: left;
				padding: .4rem 0 0 5px;
			}
		}
	}
	.booking__step--2{
		display: none;
		.guest{
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			margin-bottom: 1rem;
			.guest__item{
				flex-basis: 45%;
				padding: 0 0 0 .5rem;
				&.guest__item--trash{
					img{
						float: right;
						margin-bottom: 1rem;
					}
				}
				&:nth-child(odd){
					padding: 0 .5rem 0;
				}
			}
		}
		.medical-statement{
			font-size: .8rem;
			a{
				color: $color-green;
			}
		}
	}
	.booking__steps{
		height: 100vh;
	}
}

// calendar
.section-form-field_group--datepicker{
	.datepicker{
		display: none;
		.ui-datepicker-inline{
			position: absolute;
			top: 2.2rem;
			left: 0;
			right: 0;
			background-color: $color-white;
			box-shadow: 0 0 10px fade-out($color-darkgray, .5);
			width: 100%;
			padding: 1rem 1rem 1rem;
			box-sizing: border-box;
			z-index: 8;
			.ui-datepicker-header{
				position: relative;
				a{
					position: absolute;
					cursor: pointer;
					&:hover{
						opacity: .5;
					}
					span{
						color: $color-purple;
						font-size: .8rem;
					}
				}
				.ui-datepicker-next{
					right: 0.3rem;
				}
				.ui-datepicker-prev{
					left: 0.3rem;
				}
				.ui-datepicker-title{
					position: absolute;
					left: 0.3rem;
					margin-top: 1.5rem;
					span{
						color: $color-purple;
						font-weight: 600;
					}
				}
			}
			.ui-datepicker-calendar{
				margin-top: 3rem;
				span{
					color: $color-text;
					font-size: .8rem;
				}
			}
			table {
				width: 100%;
				td {
					font-size: .8rem;
					text-align: center;
					span {
						display: block;
						padding: .2rem;
					}
					a {
						display: block;
						padding: .2rem;
						&:link, &:visited{
							color: $color-text;
						}
						&:hover{
							background-color: $color-purple;
							color: $color-white;
						}
					}
					&.ui-state-disabled {
						span {
							color: $color-textmeta;
						}
					}
					&.ui-datepicker-today {
						a {
							background-color: $color-textmeta;
						}
					}
					&.ui-datepicker-current-day a {
						background-color: $color-purple;
						color: $color-white;
					}
				}
			}
			
		}
	}
}

body.booking-on {
	overflow: hidden;
	.booking {
		opacity: 1;
		transition: $transitionBezier;
		transform: translateX(0);
		display: block;
		z-index: 400;
	}
	#intercom-container {
		z-index: -1 !important;
	}
}
#intercom-container-body{
	background: transparent !important;
}

.booking__cover {
	position: fixed;
	right: 0;
	left: 0;
	top: 0;
	bottom: 0;
	z-index: -10;
	opacity: 0;
	background: rgba(0,0,0,0.25);
}

body.booking-on .booking__cover {
	opacity: 1;
	transition: $transitionBezier;
	transform: translateX(0);
	display: block;
	z-index: 399;
}