
// Responsive

@media (max-height: 650px) and (min-width: $screenMobile) {

	// Cards when small vertical height

	.section.card {
		height: auto;
		.container {
			padding: 3rem 0;
		}
		.col-content {
			padding-top: 0;
		}
	}

	.section.card.card--center {
		.container {
			padding: 3rem 0;
			position: relative;
			top: auto;
			transform: none;
		}
	}

	.section.card.card--grid {
		.container {
			padding: 0;
		}
	}

}


@media (max-width: $screenTablet) {
	// Tours: More tours section
	// Disable clip path on second element on mobile and tablet
	.section.section--tours--more {
		.card__col--clip-two {
			clip-path: none !important;
		}
	}
}

@media (min-width: 650px) and (max-width: $screenMobile) {

	.section--intro {
		padding-left: 0rem;
		padding-right: 0rem;
		&.section--home{
			background-position: -16rem -28rem;
			//padding-bottom: 143px;
		}
	}

	.card{
		&.card--intro{
			p{
				padding-right: 4rem;
			}
			.col-content{
				max-width: none;
			}
		}
	}

	.section{
		&.card{
			&.section--photo{
				.photos--slider{
					ul{
						li{
							span{
								display: block;
							}
						}
					}
				}
			}
		}
	}

}
// 768px
@media (min-width: $screenMobile) {

	.menu-list {
		display: block;
	}

	a.btn{
		padding: 0 2rem;
		&.button-gallery{
			padding: 0 .7rem;
		}
	}

	h1{
		font-size: 2.3rem; // 64px
	}

	html, body {
		font-size: 18px;
	}

