		.menu__logo{
			margin-left: 5.1rem;
		}
	}

	.section .container {
		padding-left: 0rem;
		padding-right: 0rem;
	}

	// Card

	.card {
		height: 100vh;
		&.card--intro {
			p{
				font-size: 1rem;
				line-height: 1.3rem;
				padding-right: 16rem;
			}
			.col-content{
				max-width: none;
			}
		}
		&.card--grid {
			p{
				font-size: 1rem;
				line-height: 1.2rem;
			}
			.card__col {
				position: relative;
			}
			.card__col--center .col-content {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				right: 0;
			}
		}
		&.card--half {
			height: 50vh;
			&:before {
				display: none;
			}
		}
		&.card--center .container {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			right: 3rem;
			.col-content {
				padding-top: 0;
			}
		}
		&.card--hover {
			.card__col--cta {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: -1;
				opacity: 0;
				padding: 0;
				transition: $transitionBezier;
				.card__col--cta-c {
					text-align: center;
					.btn {
						display: inline-block;
						position: relative;
						top: 1rem;
					}
				}

			}
			.card__col:hover .card__col--cta {
				z-index: 5;
				background: $color-darkgray;
				transition: $transitionBezier;
				opacity: 1;

				.btn {
					top: 0;
				}
			}
		}
		.card-head{
			.col-content{
				.card-head__button{
					text-align: right;
				}
			}
		}
	}

	.section--intro {
		padding-left: 4rem;
		&.section--home{
			background-position: -26rem -27rem;
			//padding-bottom: 204px;
		}
		&.section--tour{
			background-position: -16rem -21rem;
			//padding-bottom: 340px;
		}
	}

	.section--tours{
		.col-group{
