					padding: 6rem 6rem 4rem;
				}
				.card__col--cta-c{
					text-align: right;
					p{
						text-align: right;
						padding: 0;
					}
				}
				.tours__wrap {
					top: 0;
				}
				.tours__hover {
					padding: 5rem;
					.tours__hover-wrap{
						top: 0;
					}
				}
			}
		}

	}


	.page--about{
		background-position: 6rem -43rem;
		.section--about{
			background-position: -37rem -6rem;
    		padding-bottom: 154px;
			background-size: 1490px;
			p{
				padding-right: 0rem;
			}
		}
	}

	.section--why{
		padding: 3rem;
		.card-head{
			h1{
				text-align: left;
			}
		}
		.section--grid__icon {
			.section--grid__icon--icons{
				display: block;
			}
		}
	}

	.section{
		&.card{
			&.section--photo{
				.photos--slider{
					ul{
						li{
							span{
								display: block;
							}
						}
					}
				}
