// 992px
@media (min-width: $screenTablet) {

	header {
		padding-top: 4rem;
		.menu__logo{
			img{
				width: 95px;
			}
		}
	}

	.card {
		&.card--intro {
			p{
				padding-right: 3rem;
			}
		}
	}

	.booking {
		width: 22rem;
	}

	.section--intro {
		padding-left: 14rem;
		padding-top: 1rem;
		&.section--home{
			background-position: -26rem -26rem;
			//padding-bottom: 134px;
		}
		&.section--tour{
			background-position: -16rem -21rem;
			//padding-bottom: 320px;
		}
	}

	.section--tours{
		.col-group{
			.card__col{
				-webkit-transform:translate3d(0, 0, 0);
				flex-basis: 50%;
				-ms-flex-preferred-size: 50%;
				&:nth-child(odd){
					right: .4rem;
				}
				&:nth-child(1){
					margin-left: -1px;
				}
				&:nth-child(2){
					right: -1px;
				}

				&:nth-child(1){
					  -webkit-mask-image: url("../img/tours-1-mask.png");
					  mask-image: url("../img/tours-1-mask.png");
					  -webkit-mask-position: center bottom;
					  mask-position: center bottom;
					  -webkit-mask-repeat: no-repeat;
					  mask-repeat: no-repeat;
					  mask-size: 100% 96%;
					  -webkit-mask-size: 100% 96%;
				}
				&:nth-child(2) {
					-webkit-mask-image: url("../img/tours-2-mask.png");
					mask-image: url("../img/tours-2-mask.png");
					-webkit-mask-position: center center;
					mask-position: center center;
					-webkit-mask-repeat: no-repeat;
					mask-repeat: no-repeat;
					mask-size: 100% 100%;
					-webkit-mask-size: 100% 100%;
				}
				&:nth-last-child(2) {
					-webkit-mask-image: url("../img/tours-3-mask.png");
					mask-image: url("../img/tours-3-mask.png");
					-webkit-mask-position: center center;
					mask-position: center center;
					-webkit-mask-repeat: no-repeat;
					mask-repeat: no-repeat;
					mask-size: 100% 100%;
					-webkit-mask-size: 100% 100%;
				}
				&:nth-last-child(1) {
					-webkit-mask-image: url("../img/tours-4-mask.png");
					mask-image: url("../img/tours-4-mask.png");
					-webkit-mask-position: center top;
					mask-position: center top;
					-webkit-mask-repeat: no-repeat;
					mask-repeat: no-repeat;
					mask-size: 100% 96%;
					-webkit-mask-size: 100% 96%;
				}
			}
		}

		&.section--tours--more{
			.card-head__title{
				h1{
					margin-left: 0rem;
				}
			}
			.card-head__button{
				a{
					margin-right: 0rem;
				}
			}
			.col-group{
				.card__col{
					&:nth-child(1){
					  -webkit-mask-image: url("../img/tours-1-mask.png");
					  mask-image: url("../img/tours-1-mask.png");
					  -webkit-mask-position: center bottom;
					  mask-position: center bottom;
					  -webkit-mask-repeat: no-repeat;
					  mask-repeat: no-repeat;
					  mask-size: 100% 96%;
					  -webkit-mask-size: 100% 96%;
					}
					&:nth-child(2) {
						-webkit-mask-image: url("../img/tours-2-mask.png");
						mask-image: url("../img/tours-2-mask.png");
						-webkit-mask-position: center center;
						mask-position: center center;
						-webkit-mask-repeat: no-repeat;
						mask-repeat: no-repeat;
						mask-size: 100% 100%;
						-webkit-mask-size: 100% 100%;
					}
				}
			}
		}
	}

	.section--why{
		padding: 5rem 0 3rem;
	}

	.page--home{
		background-position: 17rem 8rem;
		background-size: 100%;
	}

	.page--about{
		.menu-list {
			ul{
				li{
					a{
						color: $color-white;
					}
				}
			}
		}
		background-position: 15rem -48rem;
		.section--about{
			background-position: -37rem -6rem;
    	padding-bottom: 100px;
    	background-size: 1430px;
		}
		.section--why{
			.section--grid{
				.section--grid__item{
					.section--grid__icon--icons{
						display: block;
					}
				}
			}
		}
	}

	.section{
		&.card{
			&.section--photo{
				min-height: 32rem;
				.photos--slider{
					ul{
						li{
							min-height: 33rem;
						}
					}
				}
			}
		}
	}

	// equipment
	// .section{
	// 	&.card{
	// 		&.section--equipment{
	// 			.card__col--equipment{
	// 				padding: 3rem 0 10rem;
	// 			}
	// 		}
	// 	}
	// }

	.section--sales{
		margin: 15rem 0 5rem 0;
		.section--grid__item{
			padding: 0 10rem  0 0;
		}
		.grid__item--icon{
			float: left;
		}
	}

	footer{
		.footer--col{
			.menu__logo{
				text-align: left;
			}
		}
	}
	.section--gallery{
		.main-carousel{
			&.shape{
				-webkit-mask-image: url("../img/gallery-mask.png");
				mask-image: url("../img/gallery-mask.png");
			}
		}
	}

	.page--landing{
		.section--gallery{
			.main-carousel{
				&.shape{
					-webkit-mask-image: url("../img/gallery-mask.png");
					mask-image: url("../img/gallery-mask.png");
				}
			}
		}
	}
