// 1367px
@media (min-width: $screenDesktop) {

	html, body {
		font-size: 20px;
	}

	header{
		.menu__logo{
			img{
				width: auto;
			}
		}
		
	}

	.card{
		.card-head{
			.col-content{
				.card-head__button{
					padding-right: 3rem;
					text-align: right;
				}
			}
		}
	}

	.section--intro{
		padding: 1.4rem 0 0 0;
		padding-left: 13rem;
		&.section--home{
			background-position: -3rem -20rem;
			// padding-bottom: 144px;
		}
		&.section--tour{
			background-position: -3rem -13rem;
			// padding-bottom: 320px;
		}
	}

	.section--tours{
		.card__col{
			.col-content{
				padding: 6rem;
			}
			.card__col--cta-c{
				p{
					padding: 0;
				}
			}
			.tours__hover {
				padding: 6rem;
			}
		}
		&.card{
			&.card--grid {
				.card__col{
					height: auto;
				}
			}
		}
	}

	.page--about{
		background-position: 31rem -41rem;
		.section--about{
			background-position: -30rem -7rem;
    	padding-bottom: 100px;
    	background-size: 1680px;
			p{
				padding-right: 6rem;
			}
		}
	}

	.section-form-fields{
		.section-form-field_group{
			.select__arrow--btn {
				.select__arrow {
			    	top: -7px;
		    	}
			}
	  	}
	}

	.section{
		&.card{
			&.section--photo{
				min-height: 32rem;
				.photos--slider{
					ul{
						li{
							min-height: 33rem;
						}
					}
				}
			}
		}
	}

	.section--tripadvisor{
		.tripadvisor-item{
			.col-content{
				flex-wrap: nowrap;
			}
			.section--tripadvisor-image{
				flex-basis: 30%;
			}
		}
	}

	.page--landing{
		.section--gallery{
			.main-carousel{
				&.shape{
					-webkit-mask-image: url("../img/gallery-mask.png");
					mask-image: url("../img/gallery-mask.png");
				}
			}
		}
	}

	footer{
		.footer--col{
			.menu-list--footer{
				padding: .6rem 0 0;
			}
		}
	}

}

