
a:link, a:visited {
	text-decoration: none;
	color: 4color-blue;
	transition: $transitionBezier;
}

a:hover,
a:hover i:before {
	color: $color-darkgray;
	transition: $transitionBezier;
}

i:before {
	color: $color-blue;
	vertical-align: middle;
}

a.btn {
	font-size: 1rem;
	line-height: 2em;
	color: $color-white;
	display: inline-block;
	padding: 0 1em;
	font-family: $sans;
	font-weight: 800;
	transition: $transitionBezier;
	background-color: $color-blue;
	box-sizing: border-box;
	border-radius: 1rem;
	&:hover {
		background-color: $color-white;
		color: $color-blue;
		transition: $transitionBezier;
	}
	&.btn-book{
		text-transform: uppercase;
	}
}

.btn-purple, a.btn-purple {
	color: $color-white;
	background-color: $color-purple;
	&:hover {
		color: $color-purple;
		background-color: $color-white;
	}
}

.btn-green, a.btn-green {
	color: $color-white;
	background-color: $color-green;
	&:hover {
		color: $color-green;
		background-color: $color-white;
	}
}
.btn-link, a.btn-link {
	font-size: 1rem;
	line-height: 2em;
	display: inline-block;
	padding: 0;
	color: $color-purple;
	font-family: $sans;
	font-weight: 800;
	transition: $transitionBezier;
	background-color: transparent;
	box-sizing: border-box;
	border-radius: 1rem;
	&:hover {
		color: $color-white;
		transition: $transitionBezier;
	}
}

i[class*="icon-"] {
	display: inline-block;
	margin-left: 0.25em;
}

i[class*="icon-"]:before {
	position: relative;
	margin-top: -0.5em;
}

a.btn-fill.btn-white {
	background: fade($color-darkgray, 30%);
	&:hover {
		background: $color-white;
	}
}
// gallery buttons
.button-gallery{
	cursor: pointer;
	padding: 0;
	padding: 0 .7rem;
	.flickity-button-icon{
		width: 12px;
	}
} 