// Photogrid

.photogrid {
	margin-top: 3rem;
	.col-group {
		margin-bottom: 3px;
		position: relative;
	}
	.photogrid__item {
		padding-left: 1px;
		padding-right: 2px;
		box-shadow: 0 0 108px fade($color-darkgray, 20%);
		img {
			display: block;
		}
	}
	.photogrid__item--bottom {
		position: absolute;
		bottom: 0;
		right: 0;
		img {
			position: absolute;
			bottom: 0;
			// width: ~"calc(100% - 3px)";
		}
	}
}