
/*

	Global

*/

html, body {
	height: 100%;
	font-size: 20px;
	height: 100%;
}

body {
	font-family: $sans;
	font-weight: 400;
	line-height: 1.5;
	color: $color-text;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: #fff;
}

img {
	max-width: 100%;
	height: auto;
}

.section {
	padding: 0;
	.container {
		padding-left: 2rem;
		padding-right: 2rem;
        max-width: none;
        .col-group{
            margin: 0;
        }
	}
}

.misc{
	display: inline-block;
	height: 12px;
	width: 70px;
	border-radius: 12px 12px 12px 12px;
	margin-bottom: .8rem;
	&.misc--purple{
		background-color: $color-purple;
	}
}
.misc--tours{
	position: absolute;
	z-index: 99;
	right: -70px;
	top: 220px;
	display: none;
}
