
/*

	Sections

*/

.section {
	position: relative;
}

// Home
.page--home{
	background-image: url('/img/bg-home2.png');
	background-repeat: no-repeat;
	background-position: 17rem 8rem;
	background-size: 90%;
	.menu{
		.menu__button {
			svg{
				path{
					fill: $color-purple;
				}
			}
		}
	}
	.card-head__title{
		h1{
			text-align: left;
			padding-right: 0;
		}
	}
	.section--why{
		.card-head__title{
			h1{
				margin-left: 0rem;
			}
		}
	}
}
//tours
.page--tour{
	.menu-list {
		ul{
			li{
				a{
					color: $color-white;
					&:hover{
						color: $color-blue;
					}
				}
			}
		}
	}
	.section--intro{
		color: white;
		min-height: 658px;
		p{
			margin-bottom: 2rem;
		}
		.card__col--cta-c{
			p{
				margin: 0;
				font-size: .7rem;
				font-weight: bold;
				span{
					font-weight: normal;
				}
			}
			.btn-fullscreen{
				margin-top: 1rem;
			}
		}
	}
	.section--why{
		padding-top: 0;
	}
	.section--tours{
    overflow: hidden;
		// &.section--tours--more{
		// 	.card__col{
		// 		display: none;
		// 	}
		// 	.card__col:not(:nth-child(n+3)){
		// 		display: block;
		// 	}
		// }
	}
}

// about
.page--about{
	background-image: url('/img/bg-about.jpg');
	background-repeat: no-repeat;
	background-position: -10rem -43rem;
	.section--why{
		.section--grid{
			position: relative;
			.section--grid__item{
				h1{
					margin-bottom: 2rem;
					color: $color-purple;
				}
				img{
					margin-bottom: 1rem;
				}
				.section--grid__icon--icons{
					position: absolute;
					bottom: 3rem;
					display: none;
				}
			}
		}
		
	}
}

.section--intro {
	color: $color-green;
	padding: 4rem 0 0 0;
	z-index: 2;
	.card__col--gallery-buttons{
		margin-bottom: 1rem;
	}
	&.section--tour{
		background-image: url('/img/shape-header-tour.svg');
		background-repeat: no-repeat;
		background-position: -23rem -19rem;
		padding-bottom: 160px;
		.card__col--buttons{
			margin-top: 3rem;
			.card__col--cta-c{
				margin: 0 1rem 1rem 0;
				float: left;
				p{
					text-align: center;
					font-weight: 800;
					margin: .3rem 0;
					padding: 0;
					font-size: .7rem;
					span{
						font-weight: 100;
					}
				}
			}
		}
	}
	&.section--about{
		background-image: url('/img/shape-header-about3.svg');
		background-repeat: no-repeat;
		background-position: -41rem -59rem;
		padding-bottom: 10%;
		color: $color-white;
	}
}

// tours
.section--tours{
    overflow: hidden;
	&.section--tours--more{
		.card-head__title{
			h1{
				margin-right: 1rem;
				display: inline;
			}
		}
		.card-head__button{
			text-align: right;
			a{
				margin-right: 2rem;
			}
		}
	}
	
	h1{
		span{
			color: $color-purple;
		}
	}
	span{
		font-weight: 100;
	}
	.col-group{
		box-sizing: border-box;
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: -webkit-inline-box;
		display: flex;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		-ms-flex-direction: row;
		flex-direction: row;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		.card__col{
			height: auto;
            .tours__wrap-link{
                position: absolute;
                width: 100%;
                height: 100%;
                z-index: 1;
            }
			flex-basis: 100%;
			-ms-flex-preferred-size: 100%;
			position: relative;
			color: $color-white;
			&.card__col--purple{
				h1{
					span{
						color: $color-purple;
					}
				}
				.col-content{
					background-color: $color-purple;
				}
			}
			&.card__col--blue{
				h1{
					span{
						color: $color-blue;
					}
				}
				.col-content{
					background-color: $color-blue;
				}
			}
			&.card__col--green{
				h1{
					span{
						color: $color-green;
					}
				}
				.col-content{
					background-color: $color-green;
				}
			}
			margin-bottom: .4rem;
			.col-content{
				padding: 5rem 2rem;
				height: 100%;
				position: relative;
				z-index: 0;
				display: flex;
				::after{
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
				}
				&.tours-silfra_snorkle{
					::after {
					  content: "";
					  background-image: url('/img/tour-3.jpg');
					  opacity: 1;
					  top: 0;
					  left: 0;
					  bottom: 0;
					  right: 0;
					  position: absolute;
					  z-index: -1;
					  transition: $transitionBezier;
					}
				}
				&.tours-silfra_dive{
					::after {
					  content: "";
					  background-image: url('/img/tour-4.jpg');
					  opacity: 1;
					  top: 0;
					  left: 0;
					  bottom: 0;
					  right: 0;
					  position: absolute;
					  z-index: -1;
					  transition: $transitionBezier;
					}
				}
				&.tours-ocean_dive{
					::after {
					  content: "";
					  background-image: url('/img/tour-2.jpg');
					  opacity: 1;
					  top: 0;
					  left: 0;
					  bottom: 0;
					  right: 0;
					  position: absolute;
					  z-index: -1;
					  transition: $transitionBezier;
					}
				}
				&.tours-private_tour{
					::after {
					  content: "";
					  background-image: url('/img/tour-8.jpg');
					  opacity: 1;
					  top: 0;
					  left: 0;
					  bottom: 0;
					  right: 0;
					  position: absolute;
					  z-index: -1;
					  transition: $transitionBezier;
					}
				}
				&.tours-kleifarvatn{
					::after {
					  content: "";
					  background-image: url('/img/tour-9.jpg');
					  opacity: 1;
					  top: 0;
					  left: 0;
					  bottom: 0;
					  right: 0;
					  position: absolute;
					  z-index: -1;
					  transition: $transitionBezier;
					}
				}
				&.tours-dry_suite{
					::after {
					  content: "";
					  background-image: url('/img/tour-1.jpg');
					  opacity: 1;
					  top: 0;
					  left: 0;
					  bottom: 0;
					  right: 0;
					  position: absolute;
					  z-index: -1;
					  transition: $transitionBezier;
					}
				}
			}
			p{
				padding-right: 0rem;
			}
			h1{
				text-align: left;
				padding: 0;
			}
			.card__col--cta-c{
				text-align: left;
				margin-top: 2rem;
				p{
					font-weight: 800;
					font-size: .8rem;
					padding-right: 0rem;
					margin: 0.5rem 0 0 0;
					text-align: left;
					padding-left: 5px;
				}
			}
			.tours__wrap {
				transition: $transitionBezier;
				/* min-height: 300px; */
				align-self: center;
			}
			&:hover {
				.col-content {
					transition: $transitionBezier;
					opacity: 1;
					::after{
						opacity: 0;
						transition: $transitionBezier;
					}
				}
				.tours__wrap {
					transition: $transitionBezier;
					opacity: 1;
					z-index: 65;
					color: $color-white;
					.btn{
						background-color: $color-white;
						color: $color-darkgray;
					}
					h1{
						span{
							color: $color-white;
						}
					}
				}
			}
		}
	}
}



// book
.section--book{
	.container{
		padding: 0;
	}
	.card__col--book{
		h1{
			text-align: center;
			color: $color-white;
			padding: 3rem 0 2rem 0;
			text-align: center;
		}
		min-height: 40rem;
		background: rgba(162,90,191,1);
		background: -moz-linear-gradient(45deg, rgba(162,90,191,1) 0%, rgba(63,149,203,1) 100%);
		background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(162,90,191,1)), color-stop(100%, rgba(63,149,203,1)));
		background: -webkit-linear-gradient(45deg, rgba(162,90,191,1) 0%, rgba(63,149,203,1) 100%);
		background: -o-linear-gradient(45deg, rgba(162,90,191,1) 0%, rgba(63,149,203,1) 100%);
		background: -ms-linear-gradient(45deg, rgba(162,90,191,1) 0%, rgba(63,149,203,1) 100%);
		background: linear-gradient(45deg, rgba(162,90,191,1) 0%, rgba(63,149,203,1) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a25abf', endColorstr='#3f95cb', GradientType=1 );
		// clip-path: url('#svgPath-5');
		-webkit-mask-image: url("../img/tours-4-mask.png");
		mask-image: url("../img/tours-4-mask.png");
		-webkit-mask-position: center center;
		mask-position: center top;
		-webkit-mask-repeat: no-repeat;
		mask-repeat: no-repeat;
		mask-size: 100% 100%;
		-webkit-mask-size: 100% 100%;
		position: relative;
		z-index: 9;
	}
}

// photos
.section{
	&.card{
		&.section--photo{
			background-color: $color-blue;
			margin-bottom: 3rem;
			overflow: hidden;
			max-height: 22rem;
			.container{
				padding: 0;
			}
			.unslider{
				position: relative;
				nav{
					position: absolute;
					bottom: 1rem;
					right: 50%;
					transform: translateX(50%);
					z-index: 99;
					ol{
						li{
							width: 10px;
							height: 10px;
							border-radius: 10px;
						}
					}
				}
			}
			.photos--slider{
				ul{
					li{
						position: relative;
						margin-bottom: -.5rem;
						display: block;
						background-size: cover;
						background-position: center;
						min-height: 23rem;
						span{
							display: none;
							position: absolute;
							z-index: 999;
							left: 2rem;
							bottom: 3rem;
							color: $color-white;
							width: 15rem;
							p{
								font-size: .8rem;
								line-height: 1rem;
							}
						}
					}
				}
			}
		}
	}
}

