// iceland
.section{
	&.card{
		&.section--iceland{
			background-image: url("/img/bg-iceland.jpg");
			background-size: cover;
			background-position: center;
			margin-bottom: 3rem;
			min-height: 30rem;
			margin-top: -3rem;
			color: $color-white;
			text-align: center;
			position: relative;
			padding: 4rem 0;
			.container{
				padding: 0;
				width: 100%;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				right: 0;
				.card__col--iceland{
					.col-content{
						padding: 0;
						p{
							margin-bottom: 0;
							padding: 0 2rem;
						}
					}
				}
			}
		}
	}
}
// equipment
.section--equipment{
	p{
		padding-right: 2rem;
    font-size: .8rem;
    line-height: 1.2rem;
	}
}
// Why
.section--why{
	padding: 5rem 0 0 0;
	.card-head{
		h1{
			text-align: left;
		}
	}
	&.section--why--description{
		h1{
			color: $color-purple;
		}
		ul{
			li{
				list-style: disc;
				color: $color-green;
				font-size: .8rem;
				margin-bottom: .2rem;
			}
		}
		p{
			span{
				font-size: .6rem;
				display: block;
				color: $color-green;
				border-bottom: 1px solid $color-textmeta;
				padding-bottom: .5rem;
			}
		}
	}
	h2{
		font-size: .8rem;
	}
	p{
		padding-right: 2rem;
		font-size: .8rem;
		line-height: 1.2rem;
	}
	.section--grid__button{
		float: right;
		margin: 2.5rem 0rem 0 0;
		text-align: right;
	}
	.section--grid__icon {
		padding: 0;
		margin: 2rem 0;
		.section--grid__icon--icons{
			float: left;
			display: none;
		}
		.section--grid__icon--button{
			float: left;
			margin: .3rem 1rem 0 0;
		}
	}
	.section--grid__operator{
		img{
			float: right;
		}
	}
}

.section--landingpages{
	padding: 6rem 0 0 0;
	color: $color-purple;
}

.section--tripadvisor{
	color: $color-white;
	background-color: $color-darkgray;
	padding: 3rem 0 2rem 0;
	.card-head__title{
		color: $color-green;
		margin-bottom: 0rem;
		img{
			height: 30px;
			width: auto;
			padding-top: 5px;
		}
		.col-content{
			padding: 0;
			h4{
				margin-bottom: 0;
				font-size: 2rem;
				line-height: 2rem;
			}
		}
	}
	.card-head__button{
		text-align: left;
		.col-content{
			padding: 0;
		}
	}
	.tripadvisor-item{
		a{
			&:hover{
				h4{
				color: $color-gray;
				}
			}
		}
		margin-bottom: 1rem;
		.col-content{
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
		}
		.section--tripadvisor-image{
			position: relative;
			padding-right: 1rem;
			.profile{
				border-radius: 50%;
				display: block;
				height: 60px;
				width: 60px;
				overflow: hidden;
			}
			.quotes{
				position: absolute;
				left: 2.5rem;
				top: 0;
			}
		}
		.section--tripadvisor-quote{
			padding-right: 1.5rem;
			flex-basis: 100%;
			.quotes{
				padding-left: 1rem;
			}
			h4{
				margin: 0;
				font-size: 1.3rem;
				line-height: 1.3;
				color: $color-white;
			}
			p{
				color: $color-green;
				font-weight: bold;
				margin: 0;
			}
			.star{
				img{
					width: 15px;
					height: auto;
				}
			}
		}
	}
	.reviews-carousel{
		.flickity-button{
			background: transparent;
			color: white;
			top: 20%;
			&.next{
				right: -40px;
			}
			&.previous{
				left: -40px;
			}
		}
	}
	
}

.section--gallery{
	position: absolute;
	width: 100%;
	top: 0;
	.main-carousel{
		background: black;
		z-index: 0;
		&:before{
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			background-color:rgba($color-darkgray, $alpha: .5);
			z-index: 1;
		}
		&.shape{
			-webkit-mask-image: url("../img/tours-3-mask.png");
			mask-image: url("../img/tours-3-mask.png");
			-webkit-mask-position: center center;
			mask-position: center top;
			-webkit-mask-repeat: no-repeat;
			mask-repeat: no-repeat;
			mask-size: 100% 100%;
			-webkit-mask-size: 100% 100%;
		}
		.carousel-cell {
			width: 100%; /* full width */
			height: 658px; /* height of carousel */
			margin-right: 10px;
			span{
				display: block;
				height: 100%;
				background-size: cover;
				background-repeat: no-repeat;
				background-position: center center;
			}
		}
	}

}

.page--landing{
	background-image: url('/img/bg-home.png');
	background-repeat: repeat-y;
  background-position: 29rem -81rem;
	background-size: 90%;
	.section--intro{
		color: $color-white;
		min-height: 658px;
	}
	.menu-list {
		ul{
			li{
				a{
					color: $color-white;
				}
			}
		}
	}
	.section--tours{
    overflow: hidden;
		// &.section--tours--more{
		// 	.card__col{
		// 		display: none;
		// 	}
		// 	.card__col:not(:nth-child(n+3)){
		// 		display: block;
		// 	}
		// }
	}
}

.section--sales{
	margin: 15rem 0 5rem 0;
	.section--grid__item{
		padding: 0 0rem  0 0;
	}
	.grid__item--icon{
		float: none;
		padding-right: 1rem;
	}
}